.header {
    display: flex;
    position: fixed;
    top: 0;
    width: 100%;
    height: 56px;
    padding: 0 16px;
    background-color: #FFFFFF;
    align-items: center;
    justify-content: space-between;
    box-sizing: border-box;
}

.tab {
    display: flex;
    align-items: center;
}

.icon {
    width: 24px;
    height: 24px;
    padding: 8px;
    cursor: pointer;
}

.logo {
    padding-left: 16px;
}

.center-tab {
    display: flex;
    align-items: center;
    flex: 0 1 640px;
    padding: 0 16px;
}

.input {
    height: 30px;
    border: 1px solid #D3D3D3;
    box-sizing: border-box;
    flex: 1;
}

.search-icon {
    width: 24px;
    height: 24px;
    padding: 2px 20px;
    border: 1px solid #D3D3D3;
    background-color: #F8F8F8;
    cursor: pointer;
}